import React, { Component } from "react";
import axios from "../../axios-orders";
import { connect } from "react-redux";

import * as actions from "../../store/actions/index";
import Order from "../MyOrders/Order/Order";
import Spinner from "../../components/UI/Spinner/Spinner";
import withErrorHandler from "../../hoc/withErrorHandler/withErrorHandler";

class MyOrders extends Component {
  componentDidMount() {
    this.props.onFetchOrders(this.props.token, this.props.userId);
  }

  render() {
    let orders;
    const spinner = this.props.loading ? <Spinner /> : null;
    if (this.props.orders.length !== 0) {
      orders = this.props.orders.map((order) => {
        const ing = `Meat (${order.ingredients.meat}), Bacon (${order.ingredients.bacon}), Chesse (${order.ingredients.cheese}), Salad (${order.ingredients.salad})`;
        return (
          <Order
            key={order.id}
            totalPrice={order.totalPrice}
            ingredients={ing}
          />
        );
      });
    }

    return (
      <div>
        {spinner}
        {orders}
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    orders: state.order.orders,
    loading: state.order.loadingOrders,
    token: state.auth.token,
    userId: state.auth.userId,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    onFetchOrders: (token, userId) =>
      dispatch(actions.fetchOrders(token, userId)),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withErrorHandler(MyOrders, axios));



