import React from "react";

import classes from "./Order.module.css";

const Order = (props) => {
  return (
    <div className={classes.Order}>
      <p>Ingredients: {props.ingredients}</p>
      <p>Total Price: USD $ {props.totalPrice}</p>
    </div>
  );
};

export default Order;



